/* src/pages/Projetos.css */

.projetos-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 100px;
  margin-top: 100px;
  margin-bottom: 100px;
}

.project-card {
  background-color: #282840;
  color: #e0e0e0;
  width: 300px;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  transition: transform 0.2s, box-shadow 0.2s;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 15px;
}

.project-card h3 {
  color: #9c27b0;
  margin-bottom: 10px;
}

.project-card p {
  margin: 5px 0;
}

.main-content {
  display: flex;
  flex-direction: column; 
  align-items: center;
  gap: 20px; 
  margin-top: 20px; 
}

.central-button button {
  background-color: #9c27b0;
  color: #ffffff;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

.central-button button:hover {
  transform: scale(1.1); 
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2); 
}

.central-button {
  text-decoration: none; 
  margin-bottom: 10px;
}