.experiencias-container {
    font-family: Arial, sans-serif;
    padding: 20px;
    background-color: #1a1a2e;
    color: #fff;
    min-height: 100vh;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    z-index: 10;
  }
  
  .timeline {
    position: relative;
    padding: 50px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .timeline-line {
    position: absolute;
    width: 4px;
    background-color: #6a0dad;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .timeline-item {
    position: relative;
    width: 70%;
    padding: 10px 20px;
    box-sizing: border-box;
  }
  
  .timeline-item.left {
    transform: translateX(-50%); /* Move para a esquerda */
    text-align: left; /* Alinha o texto à esquerda */
  }
  
  .timeline-item.right {
    transform: translateX(50%); /* Move para a direita */
    text-align: left; /* Pode ser mantido como está */
  }
  
  .timeline-content {
    padding: 10px;
    background-color: #1a1a1a;
    border: 2px solid #8000ff;
    border-radius: 8px;
    position: relative;
  }

  .timeline-item.left .timeline-content {
    margin-right: 50px;
  }
  
  .timeline-item.right .timeline-content {
    margin-left: 50px;
  }
  
  .expand-button {
    background: none;
    border: none;
    color: #6a0dad;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
    transition: transform 0.3s ease;
  }
  
  .expand-button:hover {
    color: #9c27b0;
  }
  
  .details {
    margin-top: 10px;
    padding: 10px;
    background-color: #2c2c3e;
    border-radius: 5px;
    border: 1px solid #6a0dad;
    color: #fff;
  }
  